.container {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    padding-bottom: 6.1rem;

}
.myHeader {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
    transition: box-shadow 0.3s ease;

    background-color: white;

}


.sticky {
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0 2px 10px #00000025;

}