$primary-text: rgb(0,145,255);
$secondary-text: rgb(30, 227, 82);
$tertiary-text: rgb(34,34,34);

$big-font-size: 2.5rem;
$medbig-font-size: 2rem;
$med-font-size: 1.2rem;
$sml-font-size: 1rem;

$text-bold: 'Quicksand-SemiBold';
$text-med: 'Quicksand-Medium';
$text-light: "Quicksand";
$large-gutter: 6vw;
$small-gutter: 2vw;

$easy-rating: rgb(0, 176, 80);
$medium-rating:rgb(255, 192, 0);
$hard-rating:rgb(0,112,192);

h1, h2 {
    font-family: "Vision-Heavy";
    color: $primary-text;
    font-size:$big-font-size;
    letter-spacing: -2px;
    margin-top: 3rem;
    margin-bottom: 1rem;
    text-transform: uppercase;
}

h2 {
font-size: $medbig-font-size;
}

h3 {
    font-size: $med-font-size;
    font-family: $text-med;
    color: rgb(34,34,34);
}
h4 {
    font-size: $sml-font-size;
    font-family: $text-light;
    color: rgb(64, 64, 64);
}

h5 {
    font-size: $sml-font-size;
    font-family: $text-bold;
    color: $primary-text;
}
p {
    font-size: $sml-font-size;
    font-family: $text-med;
}
.subtitle {
    font-size: $med-font-size;
    margin: 1rem 0;
}

.easy {
    color: $easy-rating;
    
}
.medium {
    color:  $medium-rating;
}
.hard {
    color: $hard-rating;
}

.ratingStar{
    height: 1.1rem;
}

.unavailable {
    color: rgb(155, 154, 154);
}